@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap);
body{margin:0;font-family:'Source Sans Pro', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:'Source Sans Pro', sans-serif}

.pregunta{margin:30px auto;text-align:center;margin-top:50px;color:white}.pregunta .titulo-pregunta{font-size:28px}.pregunta .opciones-preguntas{margin:30px auto;width:50%;text-align:left;font-size:18px}

.resultados{font-size:25px;display:flex;grid-gap:30px;gap:30px;align-items:center;justify-content:center;margin-top:80px;margin-bottom:20px}.boton-nuevo-intento{display:block;margin:0px auto;margin-top:60px;margin-bottom:60px}.lista-resultados-parciales{width:40%;margin:0px auto;display:flex;justify-content:flex-start;grid-gap:20px;gap:20px;align-items:center}.lista-resultados-parciales li{display:flex;flex-direction:column;align-items:center;flex:0 1 9.5%;list-style:none}.lista-resultados-parciales li img{width:50%}.lista-resultados-parciales li span{font-size:22px;font-weight:600}.lista-resultados-parciales li span.correcta{color:#56AB3E}.lista-resultados-parciales li span.incorrecta{color:#AB3E3E}

.App{background-image:url(/Hugo97Padilla/test-paises-react/static/media/fondo_inicio.514662a4.jpg);width:100%;height:100vh;background-position:left;background-size:cover;background-repeat:no-repeat;background-color:#31476e;padding:30px}.App .ingreso-caja{margin:30px auto;width:30%;height:250px;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;border:2px solid #192840}.App .ingreso-caja input{outline:none;background:none;height:40px;width:70%;background:#192840;border:1px solid #192840;color:#fff;padding-left:30px}.App .ingreso-caja .boton-login{border:1px solid #192840;background-color:#192840;color:#56AB3E}.App .titulo-inicio{width:100%;height:60px;text-align:center;padding:25px 0px}.App .titulo-inicio h3{font-size:calc( 1vw + 1.7em);font-weight:600;color:#ececec;font-family:'Source Sans Pro', sans-serif}.App .boton-siguiente{display:block;margin:0px auto;position:relative;bottom:50px}

